ante, se debe medir los resultados, estos permitirán identificar si las metas fueron alcanzadas o si por el contrario se debe modificar la estrategia.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t6_2"}},[_c('h2',[_vm._v("6.2 Ciclo de vida del producto moda")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cajon color-acento-contenido bg02 p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Al entender las oportunidades y amenazas que presenta un producto al ubicarlo en una determinada fase del ciclo de vida, es como permitirá identificar las mejores estrategias o herramientas para que dicho producto pueda mantenerse por mayor tiempo posible en el mercado (Kotler & Keller, 2012), el cual con un adecuado manejo podría llegar a ser un elemento clásico que se adapte a las distintas exigencias del tiempo y el mercado.")])])}]

